/* 달력 */
.monthSelector {padding:50px 0 30px 0;border:1px solid #e5e7e9;margin-bottom:60px;}
.yearArea > h4 {font-size: 36px;color: #333333;text-align: center;display: inline-block;margin:0 20px;}
.monthArrBtn {width: 26px;height:26px;display: inline-block;}
.monthArrBtn.prevMonth {background: url('/img/pbb/common/gray-prev-arrow.png') no-repeat center / auto;}
.monthArrBtn.nextMonth {background: url('/img/pbb/common/gray-next-arrow.png') no-repeat center / auto;}

.monthArea {margin-top:33px;}
.monthItems:not(:last-child) {margin-right:30px;}
.monthItems > a {display: inline-block;width:40px;height:40px;line-height: 40px;border-radius: 50%;color:#999da5;font-size:16px;font-weight: 600;}
.monthItems > a.active {display: inline-block;width:40px;height:40px;line-height: 40px;border-radius: 50%;color:#fff;background-color:#2c64cf;}

.eventCategoryItems:not(:last-child){margin-right:20px;}
.eventCategoryItems > span {display: inline-block;font-size:14px;}
.eventCategoryItems > span:not(.cateAvatars) {font-size:16px;}
.cateAvatars {width:30px;height: 30px;border-radius: 50%;color:#fff;line-height: 30px;text-align: center;display: inline-block;position: relative;top:-1px;}
.cateAvatars.cateAvatar010 {background-color:#3a9fc5;}
.cateAvatars.cateAvatar020 {background-color:#344c8c;}
.cateAvatars.cateAvatar030 {background-color:#518318;}
.cateAvatars.cateAvatar040 {background-color:#c94494;}
.cateAvatars.cateAvatar050 {background-color:#d96b03;}
.cateAvatars.cateAvatar060 {background-color:#3961bf;}
.cateAvatars.cateAvatar070 {background-color:#6238b4;}
.cateAvatars.cateAvatar999 {background-color:#838b9a;}

.cateAvatars::after {color:#fff;font-size: 14px;}
.cateAvatars.cateAvatar010::after {content:'공';}
.cateAvatars.cateAvatar020::after {content:'전';}
.cateAvatars.cateAvatar030::after {content:'축';}
.cateAvatars.cateAvatar040::after {content:'영';}
.cateAvatars.cateAvatar050::after {content:'문';}
.cateAvatars.cateAvatar060::after {content:'체';}
.cateAvatars.cateAvatar070::after {content:'행';}
.cateAvatars.cateAvatar999::after {content:'기';}

